<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="feedlist">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="添加记录"
      subtitle="记录"
      subtitlePath="/feedRecordList">
    </myHeader>
    
    <div class="mt4 list-block">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNumber}}
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="饲喂日期：" @click.native="selectFeedDate">
        <span class="color-text">
          {{feedDateText}}
        </span>
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="饲料记录：">
      </mt-cell>
      <div class="detail-item" v-for="(item,index) in getFeedList" :key="index">
        <div class="select-detail">
          <div class="del-icon" @click="delGetFeedItem(index)">
            <img src="@/assets/del-icon.png">
          </div>
          <div class="left">
            <span class="left-name">饲料品种：</span>
            <span @click="selectFeedType(index)">{{item.feedName}}</span>
            <span class="img" @click="selectFeedType(index)"></span>
          </div>
          <div class="right">
            <span>包数：</span>
            <input type="text" placeholder="请输入数量" v-model="item.consumeQty" @input="handlerLimit(index,$event)">
          </div>
        </div>
      </div>
      <div class="add-select" @click="addFeedType">
        <span class="add-icon">+</span>新增
      </div>
    </div>

    <SubmitBtn @click.native="submitPageData"
               :btnText="submitBtnText"
               :customStyle="myCustomStyle"></SubmitBtn>

    <div class="my-prop">
      <!--选择批次-->
      <mt-popup
        v-model="switchBatchNumPopup"
        position="bottom">
        <mt-picker :slots="batchNumSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>

      <!--选择饲料类型-->
      <mt-popup
        v-model="switchSelectFeedPopup"
        position="bottom">
        <mt-picker :slots="feedTypeSlots"
                   value-key="feedName"
                   :show-toolbar="true"
                   ref="feedTypePicker">
          <span @click="handleFeedTypeConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
    </div>

    <!--选择饲养日期-->
    <mt-datetime-picker
      ref="feedPicker"
      type="date"
      v-model="feedDate"
      @confirm="feedDateConfirm"></mt-datetime-picker>

  </div>
</template>
<script>
  import myHeader from "@/components/Header";
  import SubmitBtn from "@/components/SubmitBtn";
  import { getBatchNumberList, saveFeedRecord, getPigwashList } from '@/api/business'
  import { inputLimitNum, submitValidator } from '@/utils/validator'
  import { formatDate } from '@/utils/tool'
  import { Indicator } from 'mint-ui'
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'

  export default {
    name: "feedRecord",
    mixins: [subMitBtnMixins],
    components: {
      SubmitBtn,
      myHeader
    },
    data() {
      return {
        username: '',
        batchNumber: '',
        batchId: '',
        switchBatchNumPopup: false,
        batchNumSlots: [{
          values: [{
            id: '',
            name: ''
          }],
          textAlign: 'center'
        }],
        feedDate: new Date(),
        feedDateText: formatDate(new Date(), 'yyyy-MM-dd'),
        getFeedList: [
          {
            feedName: "",
            consumeQty: '',
            feedId: ''
          },
        ],
        switchSelectFeedPopup: false,
        currentFeedTypeIndex: 0,
        feedTypeSlots: [{
          values: [{
            feedId: '',
            feedName: ''
          }],
          textAlign: 'center'
        }],
      };
    },
    created() {
      this._getBatchNumberList();
    },
    mounted() {

    },
    methods: {
      _getBatchNumberList() {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        
        getBatchNumberList().then((res) => {
          if (res.data) {
            this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id
              };
            });
            this.batchNumber = this.batchNumSlots[0].values[0].name;
            this.batchId = this.batchNumSlots[0].values[0].id;
            this._getPigwashList(this.batchId);
          } else {
            Indicator.close();
          }
        }, () => {
          Indicator.close();
        });
      },
      _getPigwashList(batchId) {
        getPigwashList(batchId).then((res) => {
          Indicator.close();
          this.feedTypeSlots[0].values = res.data.map((item, index) => {
            return {
              feedName: item.feedName,
              feedId: item.feedId
            };
          });
          this.getFeedList[this.currentFeedTypeIndex].feedName = this.feedTypeSlots[0].values[0].feedName;
          this.getFeedList[this.currentFeedTypeIndex].feedId = this.feedTypeSlots[0].values[0].feedId;
        }, () => {
          Indicator.close();
        });
      },
      selectBatchNum() {
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: '无可选批次，请核实业务场景！',
            duration: 1000
          });
        }
      },
      handleBatchConfirm() {
        this.batchNumber = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this._getPigwashList(this.batchId);
        this.switchBatchNumPopup = false;
      },
      selectFeedDate() {
        this.$refs.feedPicker.open();
      },
      feedDateConfirm(date) {
        this.feedDateText = formatDate(date, 'yyyy-MM-dd');
      },
      addFeedType() {
        this.getFeedList.push({
          feedName: this.getFeedList[0].feedName,
          feedId: this.getFeedList[0].feedId,
          consumeQty: ''
        });
      },
      delGetFeedItem(index) {
        if (this.getFeedList.length > 1) {
          this.getFeedList = this.getFeedList.filter((item, idx) => {
            return index !== idx;
          });
        }
      },
      handleFeedTypeConfirm() {
        this.getFeedList[this.currentFeedTypeIndex].feedName = this.$refs.feedTypePicker.getValues()[0].feedName;
        this.getFeedList[this.currentFeedTypeIndex].feedId = this.$refs.feedTypePicker.getValues()[0].feedId;
        this.switchSelectFeedPopup = false;
      },
      selectFeedType(selectedIndex) {
        this.currentFeedTypeIndex = selectedIndex;
        this.switchSelectFeedPopup = !this.switchSelectFeedPopup;
      },
      handlerLimit(index, e) {
        this.getFeedList[index].consumeQty = inputLimitNum(e,1);
      },
      submitPageData() {
        const checkList = [
          {
            val: this.batchNumber,
            rule: ["required"],
            getTipText: "批次号"
          }
        ];
  
        let checkFeedList =  this.getFeedList.map((item, index) => {
          return {
            val: item.consumeQty,
            rule: ['required'],
            getTipText: '饲料包数'
          }
        });
  
        let flag = submitValidator([...checkList,...checkFeedList]);
  
        if (!flag) return false;
        
        if (!this.isDisabledSubmit) {
          this.isDisabledSubmit = true;
          this.submitBtnText = '提交中...';
  
          const pageData = {
            batchId: this.batchId,
            batchName: this.batchNumber,
            feedDate: this.feedDateText,
            feedList: this.getFeedList
          };
  
          saveFeedRecord(pageData).then((res) => {
            this.$toast({
              message: '提交成功！',
              duration: 1000
            });
    
            this.$router.push("/feedRecordList");
    
          }).catch(err => {
            this.isDisabledSubmit = false;
            this.submitBtnText = '提交';
          });
          
        }
      }
    }
  };
</script>
<style lang="scss" scoped>
  .feedlist {
    min-height: 100vh;
    background-color: #f8f8f8;
    padding: 0.12rem 0;

    .list-block {
      font-size: 0.16rem;
      background-color: #fff;

      .remove {
        color: #888;
        font-size: 0.12rem;
      }

      .img {
        width: 0.1rem;
        height: 0.06rem;
        margin-left: 0.08rem;
        background: url("~@/assets/icon-next.png") no-repeat;
        background-size: 100%;
      }
    }

    .my-prop {
      .mint-popup {
        width: 100%;
        height: 25%;
      }
  
      /deep/ .picker-toolbar {
        height: 0;
      }
  
      .sure-btn {
        position: absolute;
        top: .05rem;
        right: .05rem;
        padding: .05rem .20rem;
        font-size: .16rem;
        z-index: 99;
      }
    }

    .detail-item {
      padding-left: 0.2rem;

      .select-detail {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        height: 0.54rem;
        font-size: 0.14rem;
        border-bottom: 1px solid #dddddd;
        padding-right: 0.23rem;

        .left {
          display: flex;
          justify-content: space-between;
          align-items: center;

          .left-name {
            color: #585858;
            padding-right: 0.05rem;
          }

          .img {
            width: 0.1rem;
            height: 0.06rem;
            margin-left: 0.08rem;
            background: url("~@/assets/icon-next.png") no-repeat;
            background-size: 100%;
          }
        }

        .right {
          text-align: right;
          flex-grow: 0.5;

          input {
            width: 0.73rem;
            font-size: 0.14rem;
          }
        }
      }
    }

    .add-select {
      font-size: 0.12rem;
      color: #888;
      height: 0.3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid #ebebeb;

      .add-icon {
        margin-right: 0.1rem;
      }
    }

  }


</style>





